@import url(../../node_modules/swiper/swiper-bundle.css);
@import url(../../node_modules/choices.js/public/assets/styles/choices.min.css);
*,
*::before,
*::after {
  min-width: 0;
  min-height: 0;
  box-sizing: border-box;
}

ul,
ol {
  padding: 0;
  list-style: none;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  line-height: 1.5;
  scroll-behavior: smooth;
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeSpeed;
  text-size-adjust: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  border: none;
  appearance: none;
  background: transparent;
  font: inherit;
}

input[type=text]::-ms-clear {
  display: none;
}

:root {
  --font-base: 'ProximaNova', sans-serif;
  --dark-opacity: rgba(0, 0, 0, 0.8);
  --white: #ffffff;
  --black: #000000;
  --line: #cccccc;
  --light-gray: #f3f4f5;
  --gray: #ededed;
  --sticker-dark: #990000;
  --sticker-light: #65b040;
  --hover: #009999;
  --dark-2: #2e3237;
  --dark-3: #666666;
  --title-lg: 36px;
  --transition: 0.3s;
}

@font-face {
  font-display: swap;
  font-family: "PTRoubleSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/PTRoubleSans-Regular.woff2") format("woff2"), url("../fonts/PTRoubleSans-Regular.woff") format("woff");
}

@font-face {
  font-display: swap;
  font-family: "ProximaNova";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("../fonts/ProximaNova-Regular.woff") format("woff");
}

@font-face {
  font-display: swap;
  font-family: "ProximaNova";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/ProximaNova-Light.woff2") format("woff2"), url("../fonts/ProximaNova-Light.woff") format("woff");
}

@font-face {
  font-display: swap;
  font-family: "ProximaNova";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/ProximaNova-Semibold.woff2") format("woff2"), url("../fonts/ProximaNova-Semibold.woff") format("woff");
}

* {
  outline: none;
}

body {
  position: relative;
  color: var(--white);
  font-family: var(--font-base);
}

.container {
  max-width: 1280px;
  padding: 0 20px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
}

.title-lg {
  color: var(--black);
  font-size: var(--title-lg);
  font-weight: 100;
  letter-spacing: 0.9px;
  text-align: center;
}

@media (max-width: 1023px) {
  .title-lg {
    --title-lg: 30px;
  }
}

@media (max-width: 767px) {
  .title-lg {
    --title-lg: 22px;
  }
}

.logo {
  width: 115px;
  height: 20px;
  flex-shrink: 0;
}

.header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--dark-opacity);
}

@media (max-width: 767px) {
  .header {
    border-bottom: 1px solid var(--line);
    background-color: var(--white);
  }
}

.header__inner {
  display: flex;
  height: 80px;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 767px) {
  .header__inner {
    height: 52px;
  }
}

.header__left {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1023px) {
  .header__logo {
    order: 2;
  }
}

.header__action {
  display: flex;
  align-items: center;
  margin-left: 55px;
}

@media (max-width: 767px) {
  .header__action {
    margin-left: 0;
  }
}

.header__action-item {
  display: block;
  flex-shrink: 0;
  margin-right: 42px;
}

@media (max-width: 1023px) {
  .header__action-item {
    margin-right: 10px;
  }
}

@media (min-width: 769px) {
  .header__action-item:nth-last-child(-n+1) {
    margin-right: 0;
  }
}

@media (max-width: 767px) {
  .header__action-item {
    margin-right: 0;
  }
  .header__action-item:not(:first-child) {
    margin-left: 21px;
  }
}

.header__nav {
  margin-left: 115px;
}

@media (max-width: 1279px) {
  .header__nav {
    margin-left: 50px;
  }
  .header__nav .nav__link {
    padding: 8px 20px;
  }
}

@media (max-width: 1023px) {
  .header__nav {
    margin-left: 0;
  }
}

.header__basket {
  display: flex;
  padding: 10px;
}

@media (max-width: 767px) {
  .header__basket {
    padding: 0;
  }
}

.header__basket:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.header__basket-icon {
  display: block;
  cursor: pointer;
}

.header__basket-icon.basket-icon {
  display: block;
  stroke: var(--white);
}

@media (max-width: 767px) {
  .header__basket-icon.basket-icon {
    stroke: var(--black);
  }
}

.header__basket-icon-counter, .header__heart-icon-counter {
  padding: 3px 5px 0;
  margin-left: -10px;
  background-color: var(--dark-3);
  border-radius: 20px;
  color: var(--white);
  font-size: 13px;
  line-height: 1.2;
}

.header__heart-icon {
  display: block;
  cursor: pointer;
}

.header__heart-icon.heart-icon {
  display: block;
  stroke: var(--white);
}

@media (max-width: 767px) {
  .header__heart-icon.heart-icon {
    stroke: var(--black);
  }
}

.header__wishlist {
  display: flex;
  padding: 10px;
}

@media (max-width: 767px) {
  .header__wishlist {
    padding: 0;
  }
}

.header__wishlist:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

@media (max-width: 767px) {
  .header__lang {
    display: none;
  }
}

.nav__burger {
  display: none;
}

@media (max-width: 1023px) {
  .nav__burger {
    display: block;
    margin-right: 28px;
    margin-left: 14px;
  }
}

@media (max-width: 767px) {
  .nav__burger {
    margin-right: 25px;
    margin-left: 6px;
  }
}

.nav__container {
  padding: 0 22px;
}

.nav__list {
  display: inline-flex;
}

.nav__item {
  display: inline-block;
  flex-shrink: 0;
}

@media (max-width: 767px) {
  .nav__item {
    display: block;
  }
}

.nav__item:not(:last-child) {
  margin-right: 0;
}

.nav__item--more:hover .nav__drop {
  opacity: 1;
  visibility: visible;
}

.nav__item--more:hover .nav__link::after {
  bottom: -105%;
  opacity: 1;
  transition: ease-in 0.3s;
}

.nav__link {
  display: block;
  padding: 8px 28px;
  color: var(--white);
  cursor: pointer;
  text-decoration: none;
  transition: var(--transition) ease;
}

.nav__link:hover {
  background-color: var(--dark-opacity);
}

@media (max-width: 1023px) {
  .nav__link {
    color: var(--black);
  }
  .nav__link:hover {
    background-color: var(--dark-opacity);
    color: var(--white);
  }
}

@media (max-width: 767px) {
  .nav__link {
    font-size: 15px;
  }
}

.nav__link--drop {
  position: relative;
  display: inline-flex;
  padding: 8px 20px;
}

@media (max-width: 1023px) {
  .nav__link--drop {
    display: none;
  }
}

.nav__link--drop::before {
  position: absolute;
  bottom: -20px;
  left: 0;
  width: 110px;
  height: 24px;
  content: '';
}

.nav__link--drop::after {
  position: absolute;
  bottom: -150%;
  left: 47%;
  width: 24px;
  height: 24px;
  background-color: var(--white);
  content: '';
  opacity: 0;
  transform: rotate(45deg) translateX(-50%);
  transition: ease 0.1s;
}

.nav__link--dark {
  color: var(--black);
}

.nav__link--dark:hover {
  color: var(--white);
}

.nav__link--hidden {
  display: none;
}

@media (max-width: 767px) {
  .nav__link--mobile-border {
    position: relative;
    margin-top: 20px;
  }
  .nav__link--mobile-border::before {
    position: absolute;
    top: -5px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: var(--line);
    content: '';
  }
  .nav__link--hidden {
    display: block;
  }
}

.nav__dropdown-icon {
  margin-left: 10px;
}

.nav__drop {
  position: absolute;
  z-index: 10;
  top: 100%;
  left: 0;
  width: 100%;
  padding: 20px 0;
  background-color: var(--white);
  box-shadow: 0 11px 10px 2px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: var(--transition) ease;
  visibility: hidden;
}

@media (max-width: 1023px) and (min-width: 769px) {
  .nav__drop::after {
    position: absolute;
    top: 0;
    left: 40px;
    width: 24px;
    height: 24px;
    background-color: var(--white);
    content: '';
    transform: rotate(45deg) translateX(-50%);
  }
}

@media (max-width: 767px) {
  .nav__drop {
    top: 101%;
    width: 310px;
    padding: 0;
  }
}

.nav__drop.is-open {
  opacity: 1;
  visibility: visible;
}

.nav__drop-list {
  display: inline-flex;
  flex-wrap: wrap;
}

@media (max-width: 767px) {
  .nav__drop-list {
    display: block;
    flex-wrap: nowrap;
    padding: 20px 0;
    overflow-y: scroll;
  }
}

.nav__company {
  display: none;
}

@media (max-width: 767px) {
  .nav__company {
    display: block;
    width: 100%;
    padding: 15px 0;
    background-color: rgba(0, 0, 0, 0.2);
    color: #333333;
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 0.3px;
    text-align: center;
  }
}

.account {
  position: relative;
}

@media (max-width: 1279px) {
  .account__name {
    display: none;
  }
}

@media (max-width: 1023px) {
  .account__name {
    display: block;
  }
}

@media (max-width: 767px) {
  .account__name {
    display: none;
  }
}

.account__btn {
  display: flex;
  align-items: center;
  padding: 8px 20px;
  color: var(--white);
  cursor: pointer;
  transition: var(--transition) ease;
}

@media (max-width: 1279px) {
  .account__btn {
    padding: 8px;
  }
}

@media (max-width: 767px) {
  .account__btn {
    padding: 0;
  }
}

.account__btn::before {
  position: absolute;
  bottom: -25px;
  left: 0;
  width: 100%;
  height: 25px;
  content: '';
}

.account__btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--white);
}

.account__user-icon {
  display: block;
  flex-shrink: 0;
  margin-right: 10px;
}

@media (min-width: 1025px) and (max-width: 1279px) {
  .account__user-icon {
    margin-right: 0;
  }
}

@media (max-width: 1023px) {
  .account__user-icon {
    display: none;
  }
}

.account__avatar {
  display: none;
}

@media (max-width: 1023px) {
  .account__avatar {
    display: block;
    margin-right: 10px;
  }
}

@media (max-width: 767px) {
  .account__avatar {
    margin-right: 0;
  }
}

.account__drop {
  position: absolute;
  top: 60px;
  left: 50%;
  min-width: 307px;
  padding: 43px 22px;
  background-color: var(--white);
  box-shadow: 0 0 17px 3px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transform: translateX(-51.3%);
  transition: var(--transition) ease;
  visibility: hidden;
}

@media (max-width: 1023px) {
  .account__drop {
    top: 69px;
  }
}

@media (max-width: 767px) {
  .account__drop {
    top: 59px;
    min-width: 250px;
    max-width: 250px;
    padding: 20px 10px;
  }
}

.account__drop.is-open {
  opacity: 1;
  visibility: visible;
}

.account__drop::before {
  position: absolute;
  top: 0;
  left: 50%;
  width: 24px;
  height: 24px;
  background-color: var(--white);
  content: '';
  transform: rotate(45deg) translateX(-50%);
}

@media (max-width: 1023px) {
  .account__drop::before {
    top: 3px;
  }
}

.account__menu-group {
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  margin-bottom: 25px;
}

@media (max-width: 767px) {
  .account__menu-group {
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
}

.account__menu-item {
  display: block;
}

.account__menu-link {
  display: block;
  padding: 15px 20px;
  color: var(--dark-2);
  font-size: 17px;
  text-decoration: none;
  transition: var(--transition) ease;
}

.account__menu-link:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--white);
}

@media (max-width: 767px) {
  .account__menu-link {
    padding: 10px 15px;
    font-size: 15px;
  }
}

.account__btn-close {
  display: flex;
  width: 100%;
  align-items: center;
  padding: 15px 20px;
  cursor: pointer;
  font-size: 17px;
  text-align: left;
  transition: var(--transition) ease;
}

.account__btn-close:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--white);
}

.account__btn-close:hover .account__logout-icon {
  fill: var(--white);
}

@media (max-width: 767px) {
  .account__btn-close {
    padding: 10px 15px;
    font-size: 15px;
  }
}

.account__logout-icon.logout-icon {
  padding-bottom: 2px;
  margin-right: 7px;
}

.lang {
  position: relative;
}

.lang__btn {
  padding: 10px;
  color: var(--white);
  cursor: pointer;
  font-size: 16px;
  text-align: left;
  text-decoration: none;
}

.lang__btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
  transition: background-color var(--transition ease);
}

.lang__link {
  position: relative;
  padding: 10px;
  color: var(--black);
  cursor: pointer;
  font-size: 16px;
  text-align: left;
  text-decoration: none;
  transition: var(--transition) ease;
}

.lang__link:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--white);
}

.lang__drop {
  position: absolute;
  z-index: 10;
  top: 62px;
  left: 50%;
  padding: 15px 10px;
  background-color: var(--white);
  box-shadow: 0 0 17px 3px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transform: translateX(-50%);
  transition: var(--transition) ease;
  visibility: hidden;
}

.lang__drop::before {
  position: absolute;
  top: -10px;
  left: 18px;
  width: 24px;
  height: 24px;
  background-color: var(--white);
  content: '';
  transform: rotate(45deg);
}

.lang__drop.is-open {
  opacity: 1;
  visibility: visible;
}

.hero__slide {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero__body {
  max-width: 580px;
  flex-direction: column;
  justify-content: center;
  padding-top: 245px;
  padding-bottom: 100px;
}

@media (max-width: 1023px) {
  .hero__body {
    padding-top: 150px;
  }
}

@media (max-width: 767px) {
  .hero__body {
    padding-top: 97px;
    padding-bottom: 150px;
  }
}

.hero__subtitle {
  display: block;
  width: fit-content;
  padding: 2px 27px;
  margin-bottom: 8px;
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--white);
  font-size: 18px;
  letter-spacing: 0.45px;
}

@media (max-width: 1023px) {
  .hero__subtitle {
    padding: 2px 14px;
  }
}

@media (max-width: 767px) {
  .hero__subtitle {
    margin-bottom: 2px;
    font-size: 14px;
  }
}

.hero__title {
  display: inline;
  padding: 0 27px;
  background: rgba(0, 0, 0, 0.8);
  box-decoration-break: clone;
  color: var(--white);
  font-size: 49px;
  font-weight: 300;
  letter-spacing: 1.23px;
  line-height: 1.3;
}

@media (max-width: 1023px) {
  .hero__title {
    padding: 0 10px;
    line-height: 1.3;
  }
}

@media (max-width: 767px) {
  .hero__title {
    font-size: 24px;
    line-height: 1.25;
  }
}

.hero__text {
  max-width: 390px;
  padding: 10px 27px;
  margin-top: 10px;
  margin-bottom: 45px;
  background-color: rgba(0, 0, 0, 0.8);
  box-decoration-break: clone;
  color: var(--white);
  font-size: 22px;
  letter-spacing: 0.55px;
  line-height: 1.4;
}

@media (max-width: 767px) {
  .hero__text {
    padding: 5px 5px;
    margin-top: 2px;
    margin-bottom: 8px;
    font-size: 16px;
  }
}

.hero__btn {
  display: inline-block;
  min-width: 217px;
  padding: 12px 70px;
  border: 1px solid var(--white);
  background-color: rgba(0, 0, 0, 0.8);
  color: var(--white);
  font-size: 17px;
  text-decoration: none;
  transition: background-color var(--transition) ease;
}

.hero__btn:hover {
  background-color: black;
}

@media (max-width: 767px) {
  .hero__btn {
    min-width: 173px;
    padding: 10px 55px;
    font-size: 13px;
  }
}

.swiper-container-hero {
  position: relative;
  overflow: hidden;
  width: 100%;
}

@media (max-width: 1023px) {
  .swiper-container-hero {
    height: fit-content;
  }
}

.swiper-container-hero .swiper-slide {
  width: 100%;
}

.swiper-container-hero .swiper-pagination-bullets {
  z-index: 3;
  bottom: 72px;
}

.swiper-container-hero .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 11px;
}

@media (max-width: 1023px) {
  .swiper-container-hero .swiper-pagination-bullets {
    bottom: 50px;
  }
}

@media (max-width: 1023px) {
  .swiper-container-hero .swiper-pagination-bullets {
    bottom: 37px;
  }
}

.swiper-container-hero .swiper-pagination-bullet {
  padding: 6px;
  background-color: transparent;
  border-radius: 50%;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.35), 0 0 0 2px var(--white) inset;
  opacity: 1;
}

.swiper-container-hero .swiper-pagination-bullet-active {
  width: 11px;
  height: 11px;
  background-color: var(--white);
  border-radius: 50%;
  opacity: 1;
}

.search__icon {
  position: relative;
  display: block;
  padding: 10px;
  cursor: pointer;
}

@media (max-width: 767px) {
  .search__icon {
    padding: 0;
  }
}

.search__icon::before {
  position: absolute;
  bottom: -20px;
  left: 50%;
  width: 200%;
  height: 20px;
  content: '';
  transform: translateX(-50%);
}

.search__icon:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.search__drop {
  position: absolute;
  top: 80px;
  left: 0;
  width: 100%;
  height: 0;
  padding: 0;
  background-color: #9febeb;
  opacity: 0;
  transform: translateY(-300%);
}

.search__drop.is-open {
  height: auto;
  padding: 35px;
  opacity: 1;
  transform: translateY(0);
}

.search__drop.is-open .search__field-wrapper {
  transform: translateY(0);
}

@media (max-width: 767px) {
  .search__drop.is-open {
    padding: 16px 10px;
  }
}

@media (max-width: 767px) {
  .search__drop {
    top: 52px;
  }
}

.search__field-wrapper {
  position: relative;
  display: flex;
  max-width: 900px;
  justify-content: center;
  margin: 0 auto;
  transform: translateY(-300%);
}

.search__field {
  display: block;
  width: 100%;
  padding-top: 15px;
  padding-right: 110px;
  padding-bottom: 15px;
  padding-left: 26px;
  background-color: var(--white);
  color: var(--dark-2);
  font-size: 24px;
  transition: transform var(--transition) linear;
}

@media (max-width: 767px) {
  .search__field {
    padding-top: 8px;
    padding-right: 12px;
    padding-bottom: 8px;
    padding-left: 12px;
    font-size: 16px;
  }
}

.search .icon-search {
  width: 20px;
  height: 20px;
  fill: var(--white);
}

@media (max-width: 767px) {
