/**************************\
  Basic Modal Styles
\**************************/
/**************************\
    Demo Animation Style
  \**************************/
@keyframes 'mmfadeIn' {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes 'mmfadeOut' {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes 'mmslideIn' {
  from {
    transform: translateY(15%);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes 'mmslideOut' {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-10%);
  }
}

.modal {
  z-index: 100;
}

.modal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.6);
}

.modal__container {
  width: 720px;
  max-width: 90%;
  height: 480px;
  background-color: transparent;
  border-radius: 4px;
}

.modal__close {
  position: absolute;
  top: -50px;
  right: -50px;
  width: 40px;
  height: 40px;
  padding: 0;
  cursor: pointer;
}

@media (max-width: 767px) {
  .modal__close {
    right: 50%;
    transform: translateX(50%);
  }
}

.modal__close:hover::before, .modal__close:hover::after {
  background-color: var(--hover);
}

.modal__close::before, .modal__close::after {
  position: absolute;
  top: 0;
  width: 2px;
  height: 40px;
  background-color: var(--white);
  content: '';
  transition: background-color var(--transition) ease;
}

.modal__close::before {
  transform: rotate(45deg);
}

.modal__close::after {
  transform: rotate(-45deg);
}

.modal-video {
  position: relative;
  display: none;
  width: 720px;
  max-width: 90%;
  height: 480px;
}

.modal-video .modal__container {
  will-change: transform;
}

.modal-video .modal__overlay {
  will-change: transform;
}

.modal-video.is-open {
  display: block;
}

.modal-video[aria-hidden='false'] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.modal-video[aria-hidden='false'] .modal__container {
  animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.modal-video[aria-hidden='true'] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.modal-video[aria-hidden='true'] .modal__container {
  animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

