.new-works {

  --msnry-gutter: 45px;

  @include media('<desktop-sm') {
    --msnry-gutter: 13px;
  }

  @include media('<desktop-sm') {
    --msnry-gutter: 7px;
  }

  padding-top: 47px;
  padding-bottom: 62px;
  background-color: var(--light-gray);

  @include media('<desktop-sm') {
    padding-top: 30px;
    padding-bottom: 50px;
  }

  @include media('<tablet') {
    padding-bottom: 36px;
  }

  &__title {
    margin-bottom: 9px;

    @include media('<tablet') {
      margin-bottom: 15px;
    }
  }

  &__wrapper {
    position: relative;
    margin: 0 20px;
  }

  &__sort {
    position: absolute;
    right: 0;
    bottom: 5px;
    display: flex;
    align-items: center;

    @include media('<desktop') {
      bottom: -80px;
    }

    @include media('<tablet') {
      bottom: -100px;
      flex-wrap: wrap;
      padding-top: 10px;
    }

    & .sort__label {
      margin-right: 20px;
      color: var(--black);

      @include media('<tablet') {
        margin-bottom: 5px;
      }
    }
  }

  &__section-line {
    margin-bottom: 50px;

    @include media('<desktop') {
      margin-bottom: 100px;
    }
  }

  &__grid {
    position: relative;
    width: 1240px;
    max-width: 100%;
    margin-right: auto;
    margin-bottom: 20px;
    margin-left: auto;

    @include media('<desktop-sm') {
      margin-bottom: 0;
    }
  }

  &__grid-item-sizer,
  &__grid-item,
  &__grid-item-fixed-card {
    width: calc((100% - var(--msnry-gutter) * 3) / 4);
  }

  &__grid-item {
    margin-bottom: 32px;

    @include media('<tablet') {
      margin-bottom: 24px;
    }

    // &:nth-child(-n+2) {
    //   display: none !important;
    // }
  }

  &__grid-item--empty {
    height: 1px;
    border: none;
    margin-bottom: 0;
    background-color: transparent;
  }

  @media (max-width: 1279px) {
    &__grid-item-sizer,
    &__grid-item,
    &__grid-item-fixed-card {
      width: calc((100% - var(--msnry-gutter) * 2) / 3);
    }
  }

  @media (max-width: 767px) {
    &__grid-item-sizer,
    &__grid-item,
    &__grid-item-fixed-card {
      width: calc((100% - var(--msnry-gutter)) / 2);
    }
  }

  &__grid-item-gutter {
    width: var(--msnry-gutter);
  }

  &__grid-item-fixed-card {
    position: absolute;
    top: 0;
    right: 0 !important;
    height: 480px;
    margin-bottom: 32px;
  }

  @media (max-width: 767px) {
    &__grid-item-fixed-card {
      display: none;
    }
  }

  &__btn {
    display: block;
    min-width: 218px;
    padding: 12px 70px;
    border: 1px solid #999999;
    margin: 0 auto;
    color: var(--black);
    cursor: pointer;
    font-size: 17px;

    transition: var(--transition) ease;

    &:hover {
      border: 1px solid var(--black);
      background-color: var(--black);
      color: var(--white);
    }

    @include media('<tablet') {
      min-width: 100%;
    }
  }
}

@media (max-width: 767px) {
  .hidden-mobile-down {
    display: none !important;
  }
}

@media (max-width: 1279px) {
  .hidden-tablet-down {
    display: none !important;
  }
}

@media (min-width: 1280px) {
  .hidden-desktop-up {
    display: none !important;
  }
}

.js-fixed-card {
  position: absolute;
  top: 0;
  right: 0;
}
